// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
Vue.config.productionTip = false;

import Vue from 'vue';
import axios from 'axios';        //  axios AJAX 
import VueAxios from 'vue-axios'; //  axios  Vue 
import Loading from 'vue-loading-overlay'; // loading 
import VeeValidate from 'vee-validate';
import zh_TW from 'vee-validate/dist/locale/zh_TW';

import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap'; // bootstrap js

import Alert from '@/components/backend/AlertMessage';
import Pagination from '@/components/backend/Pagination';

import App from './App';       //  App.vue  
import router from './router'; //  router/index.js
import './bus';
import currencyFilter from './filters/currency';
import dateFilter from './filters/date';

//  component
Vue.use(VueAxios, axios); //  axios 
Vue.use(VeeValidate);     // 
// Vue.use(VeeValidate, {
//   events: 'input|blur',
// });
VeeValidate.Validator.localize('zh_TW', zh_TW);

Vue.component('Alert', Alert);
Vue.component('Pagination', Pagination);
Vue.component('Loading', Loading); 

Vue.filter('currency', currencyFilter);
Vue.filter('date', dateFilter);

axios.defaults.withCredentials = true;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App }, // App.vue
  template: '<App/>',
  router,              // router: router 
});
// 
router.beforeEach((to, from, next) => {
  console.log('to',to,'from',from,'next',next);
  
  if(to.meta.requiresAuth) {  //  meta.requiresAuth  
    // alert('');
    const api =`${process.env.API_PATH}/api/user/check`;
    axios.post(api).then((response) => {
      console.log('',response.data);
      if (response.data.success) { // 
        next();
      } else {  //  login 
        next({
          path: '/login',
        });
      }
    });
  } else {
    next();
  }
});


