<template>
  <div>
    <Loading :active.sync="isLoading"></Loading>
    
    <div class="container">
      <!--  -->
      <section class="form-row text-center">
        <div class="col-12 col-sm">
          <div class="alert alert-success alert-rounded" role="alert">
            1.
          </div>
        </div>
        <div class="col-12 col-sm">
          <div class="alert alert-main alert-rounded" role="alert">
            2.
          </div>
        </div>
        <div class="col-12 col-sm">
          <div class="alert alert-main alert-rounded" role="alert">
            3.
          </div>
        </div>
      </section>

      <!--  -->
      <div class="row justify-content-center text-lighter my-3">
        <form class="col-md-8" @submit.prevent="createOrder">
          <h3 class="text-center p-3"></h3>
          <div class="row">
            <div class="col-md-6">
              <div class="form-group">
                <label for="username">*</label>
                <input type="text" class="form-control" name="name" id="username"
                  :class="{'is-invalid': errors.has('name')}" v-validate="'required'"
                  v-model="form.user.name" placeholder="">
                <small class="text-danger" v-if="errors.has('name')"> !</small>
              </div>
            </div>
            <div class="col-md-6">
              <div class="form-group">
                <label for="usertel">*</label>
                <input type="tel" class="form-control" name="Phone" id="usertel"
                :class="{'is-invalid': errors.has('Phone')}" v-validate="'required|numeric'"
                v-model="form.user.tel" placeholder="">
                <small class="text-danger" v-if="errors.has('Phone')"> !</small>
              </div>
            </div>
          </div>
          <div class="form-group">
            <label for="useremail">*Email</label>
            <input type="email" class="form-control" name="email" id="useremail"
              :class="{'is-invalid': errors.has('email')}" v-validate="'required|email'" 
              v-model="form.user.email" placeholder=" Email">
            <small class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</small>
          </div>          
          <div class="form-group">
            <label for="useraddress">*</label>
            <input type="address" class="form-control" name="address" id="useraddress" 
            :class="{'is-invalid': errors.has('address')}" v-validate="'required'" 
            v-model="form.user.address" placeholder="">
            <small class="text-danger" v-if="errors.has('address')"> !</small>
          </div>
        
          <div class="form-group">
            <label for="usermessage"></label>
            <textarea name="message" id="usermessage" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
          </div>
          <button class="btn btn-lighter btn-block btn-lg card-btn my-3">
            <i class="far fa-address-card fa-lg mr-3"></i>
          </button>        
        </form>
      </div>
    </div>    
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {         // 
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
      isLoading: false,  // true: start loading , false: stop loading      
    }
  },
  methods:{
    createOrder() { // 
      const vm = this;
      const api =`${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/order`;
      const order = vm.form;
      vm.isLoading = true;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(api, {data: order}).then((response) => {
            console.log('', response);
            if (response.data.success) {
              vm.$bus.$emit('update-cart');
              vm.$router.push(`/checkout/${response.data.orderId}`); // , 
            }
            vm.isLoading = false;
          });
        } else {
          vm.isLoading = false;
          console.log('');
        }
      });      
    },
  }  
}
</script>


