<template>
  <div>
    <!-- loading  -->
    <Loading :active.sync="isLoading"></Loading>

    <div class="text-right mt-4">
      <button class="btn btn-primary text-white" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <!-- <th width="200">ID</th> -->
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="90"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <!-- <td>{{ item.id }}</td> -->
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.origin_price | currency }}</td>
          <td class="text-right">{{ item.price | currency }}</td>
          <td >
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group ">
              <button class="btn btn-outline-basic btn-sm" @click="openModal(false, item)"></button>
              <button class="btn btn-outline-danger btn-sm" @click="delProductModal(item)"></button>
            </div>                 
          </td>
        </tr>
      </tbody>       
    </table>

    <!--  -->
    <Pagination :Pages="pagination" @getPage="getProducts"></Pagination>

    <!-- / Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header text-white" :class="{'bg-primary':isNew,'btn-basic':!isNew}">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="isNew == true"></span>
              <span v-else></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image" placeholder="" v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile"> 
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile">
                </div>
                <img class="img-fluid" alt="" :src="tempProduct.imageUrl">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder="" v-model="tempProduct.title">
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category" placeholder="" v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="unit"></label>
                    <input type="unit" class="form-control" id="unit" placeholder="" v-model="tempProduct.unit">
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price" placeholder="" v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price" placeholder="" v-model="tempProduct.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description" placeholder="" v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content" placeholder="" v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn text-white" :class="{'btn-primary':isNew,'btn-basic':!isNew}" 
              @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal-->
    <DeleteModal :Product="tempProduct" @expunge="delProduct"></DeleteModal>
  </div>
</template>

<script>
import $ from 'jquery';
import DeleteModal from '../DeleteModal';
export default {
  data() {
    return {
      products: [],     // 
      pagination: {     // 
        current_page: '',
      },   
      tempProduct: {},  // /
      isNew: false,     // true: , false: 
      isLoading: false, // true: start loading , false: stop loading 
      status: {
        fileUploading: false,  // true: show spin icon, false: hide spin icon     
      },
    };
  },
  components: { // 
    DeleteModal,
  },
  methods: {
    getProducts(page = 1) {   // 
      // const api = 'https://vue-course-api.hexschool.io/api/charles/products';
      const vm = this;
      const api =`${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/products?page=${page}`;
      vm.isLoading = true;
      
      this.$http.get(api).then((response) => {
        console.log('', response.data);
        vm.products = response.data.products;     // 
        vm.pagination = response.data.pagination; // 
        vm.isLoading = false;
      });
    },
    openModal(isNew, item) {  //  &  Modal
      const vm = this;
      if (isNew) {           // 
        vm.tempProduct = {};
        vm.isNew = true;
      } else {               // 
        vm.tempProduct = Object.assign({}, item); //  item == tempProduct
        vm.isNew = false;
      }
      console.log('openModal', isNew);
      $('#productModal').modal('show');
    },
    updateProduct() {         //  & 
      const vm = this;
      let api =`${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/product`; //  API
      let httpMethod = 'post';
      let showPage = 1; // ,   
      let failMsg =  ''   

      if (!vm.isNew) { //  API method
        api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/product/${vm.tempProduct.id}` //  API
        httpMethod = 'put';
        showPage = vm.pagination.current_page;  // , 
        failMsg = '';
      };

      this.$http[httpMethod](api, {data: vm.tempProduct}).then((response) => {
        console.log('', response.data);
        if (response.data.success) {
          $('#productModal').modal('hide');
          this.$bus.$emit('push-msg', response.data.message, 'success');
          vm.getProducts(showPage);
        } else {
          $('#productModal').modal('hide');
          this.$bus.$emit('push-msg', failMsg, 'danger');
          vm.getProducts();
        };
      });
    },
    delProductModal(item) {   //  Modal
      const vm = this;
      vm.tempProduct = Object.assign({}, item);
      $('#delProductModal').modal('show');
    },
    delProduct() {            // 
      const vm = this;
      const api =`${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/product/${vm.tempProduct.id}`;
      
      this.$http.delete(api).then((response) => {
        console.log('', response.data);
        if (response.data.success) {
          $('#delProductModal').modal('hide');
          this.$bus.$emit('push-msg', response.data.message, 'success');
          vm.getProducts(vm.pagination.current_page); // , 
        } else {
          $('#productModal').modal('hide');
          this.$bus.$emit('push-msg', response.data.message, 'danger');
          vm.getProducts();
        }        
      });
    },
    uploadFile() {            // 
      // console.log(this);
      const vm = this;
      const uploadedFile = vm.$refs.files.files[0];
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile); // append(, )

      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/upload`;
      vm.status.fileUploading = true;
      this.$http.post(api, formData, {
        headers: {
          'Content-Type': 'multipart/form-data' // 
        }
      }).then((response) => {
        // console.log('', response.data);
        vm.status.fileUploading = false;

        if (response.data.success) {
          // vm.tempProduct.imageUrl = response.data.imageUrl;
          // console.log(vm.tempProduct);
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl); //  response.data.imageUrl 
        } else {
          this.$bus.$emit('push-msg', response.data.message, 'danger'); //  alert 
        };
      });
    },
  },
  created() {
    this.getProducts();
    // this.$bus.$emit('push-msg', 'This is alert text !!'); // alert 
  },
};
</script>


